export default {
	data() {
		return {
			len:0,
			startX:0,
			moveEndX:0,
			valueX:0,
			Record:{
				Ordernumber:'',//订单号
				Namestation:'',//油站名称
				OrderPrice:'',//订单金额
				Oiltype:'',//油品类型
				couponcode:'',//电子券码
				VoucherStatus:'',//电子券状态
				UnitPrice:'',//单价
				Consumptionrise:'',//消费升数
				offtime:''//核销时间
			}
			
		}
	},
	methods: {
	touchstart(event){
		event.preventDefault();
	　　this.startX = event.touches[0].pageX
	},
	touchmove(event){
		event.preventDefault();
		this.moveEndX = event.touches[0].pageX
		this.valueX= this.moveEndX - this.startX
		if ( this.valueX >= 80 ) {
			uni.navigateBack({
				delta:1,
				animationType: 'pop-in',
				animationDuration: 200
			})	
		}
	},
	}
}
